import React, { useState } from 'react'
import { Form, Button, Input, Divider } from 'antd'

import { useAuth, useAsync, useUnMount, useDocumentTitle } from '@/hooks'
import ErrorBox from '@/components/error-box'

import { Container, CardWrapper, Logo, LongButton, ErrorWrapper } from './style'

type Tab = 'login' | 'register'

// antd

const Login: React.FC = (props) => {
  const { login, register } = useAuth()
  const [form] = Form.useForm()

  const [currentTab, setCurrentTab] = useState<Tab>('login')
  const [error, setError] = useState<Error | null>(null)
  const { run, isLoading } = useAsync(undefined, { throwOnError: true })

  useUnMount()
  useDocumentTitle('')

  const handleSubmit = async ({
    cpassword,
    ...value
  }: {
    username: string
    password: string
    cpassword?: string
  }) => {
    if (currentTab === 'register') {
      if (value.password !== cpassword) {
        return setError(new Error(''))
      }
    }
    const request = currentTab === 'login' ? login : register
    // run(request(value))
    try {
      // trycatchawait
      // await request(value)
      await run(request(value))
      // console.log(res)
    } catch (e: any) {
      setError(e)
    }
  }

  const changeTab = () => {
    setCurrentTab(currentTab === 'login' ? 'register' : 'login')
    setError(null)
    form.resetFields()
  }

  return (
    <Container>
      <CardWrapper>
        <Logo />
        <ErrorWrapper>
          <ErrorBox error={error} />
        </ErrorWrapper>
        <Form form={form} onFinish={(value) => handleSubmit(value)}>
          <Form.Item name="username" rules={[{ required: true, message: '' }]}>
            <Input autoComplete="" placeholder="" type="text" id="username" />
          </Form.Item>
          <Form.Item name="password" rules={[{ required: true, message: '' }]}>
            <Input autoComplete="" placeholder="" type="password" id="password" />
          </Form.Item>
          {currentTab === 'register' && (
            <Form.Item name="cpassword" rules={[{ required: true, message: '' }]}>
              <Input
                autoComplete="new-password"
                placeholder=""
                type="password"
                id="cpassword"
              />
            </Form.Item>
          )}
          <LongButton loading={isLoading} htmlType="submit" type="primary">
            {currentTab === 'login' ? '' : ''}
          </LongButton>
        </Form>
        <Divider />
        <Button type="link" onClick={changeTab}>
          {currentTab === 'login' ? '' : ''}
        </Button>
      </CardWrapper>
    </Container>
  )
}

export default Login

