import React, { useEffect } from 'react'
import { Drawer, Spin, Form, Input, Button } from 'antd'
import { useProjectModal, useProjectQueryKey } from '@/hooks'
import UserSelect from '@/components/user-select'
import ErrorBox from '@/components/error-box'
import { useAddProject, useEditProject } from '@/service/projects'
import { useForm } from 'antd/lib/form/Form'

import { ProjectModalWrapper } from './style'

const ProjectModal = () => {
  const { projectModalOpen, close, editingProject, isLoading } = useProjectModal()
  const title = editingProject ? '' : ''

  const useMutateProject = editingProject ? useEditProject : useAddProject
  const { mutateAsync, error, isLoading: mutateLoading } = useMutateProject(useProjectQueryKey())

  const [form] = useForm()
  const onFinish = (values: any) => {
    mutateAsync({ ...editingProject, ...values }).then(() => {
      form.resetFields()
      close()
    })
  }

  const closeModal = () => {
    form.resetFields()
    close()
  }

  useEffect(() => {
    form.setFieldsValue(editingProject)
  }, [editingProject, form])

  return (
    <Drawer forceRender={true} onClose={closeModal} visible={projectModalOpen} width="100%">
      <h2>{title}</h2>
      <ErrorBox error={error} />
      <ProjectModalWrapper>
        {isLoading ? (
          <Spin size="large" />
        ) : (
          <>
            <Form form={form} layout={'vertical'} onFinish={onFinish}>
              <Form.Item
                label=""
                name="name"
                rules={[
                  {
                    required: true,
                    message: ''
                  }
                ]}
              >
                <Input placeholder="" />
              </Form.Item>
              <Form.Item label="" name="organization">
                <Input placeholder="" />
              </Form.Item>
              <Form.Item
                label=""
                name="personId"
                rules={[
                  {
                    required: true,
                    message: ''
                  }
                ]}
              >
                <UserSelect defaultOptionName="" />
              </Form.Item>
              <Form.Item className="modal-button">
                <Button loading={mutateLoading} type="primary" htmlType="submit">
                  
                </Button>
              </Form.Item>
            </Form>
          </>
        )}
      </ProjectModalWrapper>
    </Drawer>
  )
}

export default ProjectModal
