import { useState, useEffect } from 'react'

export const useDebounce = <V>(value: V, delay: number = 1000) => {
  const [debounceValue, setDebounceValue] = useState(value)

  useEffect(() => {
    let timer: any
    timer = setTimeout(() => {
      setDebounceValue(value)
    }, delay)

    // updateuseEfffect
    return () => {
      clearTimeout(timer)
    }
  }, [value, delay])

  return debounceValue
}
