import { useState, useCallback, useReducer } from 'react'
import { useMountedRef } from './useMount'

interface AsyncState<D> {
  error: Error | null
  data: D | null
  stat: 'idle' | 'loading' | 'error' | 'success'
}

const defaultInitialState: AsyncState<null> = {
  stat: 'idle',
  data: null,
  error: null
}

const defaultConfig = {
  throwOnError: false
}

const useSafeDispatch = <T>(dispatch: (...args: T[]) => void) => {
  const { mountedRef } = useMountedRef()

  return useCallback(
    (...args: T[]) => {
      mountedRef.current ? dispatch(...args) : void 0
    },
    [mountedRef, dispatch]
  )
}

export const useAsync = <D>(initialState?: AsyncState<D>, initialConfig?: typeof defaultConfig) => {
  const config = {
    ...defaultConfig,
    ...initialConfig
  }
  // const [state, setstate] = useState({
  //   ...defaultInitialState,
  //   ...initialState
  // })
  //  useReducer statestate
  const [state, dispatch] = useReducer(
    (state: AsyncState<D>, action: Partial<AsyncState<D>>) => ({ ...state, ...action }),
    {
      ...defaultInitialState,
      ...initialState
    }
  )

  // const { mountedRef } = useMountedRef()
  const safeDispatch = useSafeDispatch(dispatch)

  // useState
  const [retry, setRetry] = useState(() => () => {})

  const setData = useCallback(
    (data: D) =>
      safeDispatch({
        data,
        stat: 'success',
        error: null
      }),
    [safeDispatch]
  )

  const setError = useCallback(
    (error: Error) =>
      safeDispatch({
        error,
        stat: 'error',
        data: null
      }),
    [safeDispatch]
  )

  const run = useCallback(
    (promise: Promise<D>, runConfig?: { retry: () => Promise<D> }) => {
      if (!promise || !promise.then) {
        throw new Error('promise')
      }

      // 
      // promisepromsie
      setRetry(() => () => {
        runConfig?.retry && run(runConfig.retry(), runConfig)
      })

      // useCallbackstate
      // setstate((preState) => ({ ...preState, stat: 'loading' }))
      safeDispatch({ stat: 'loading' })
      return promise
        .then((data) => {
          setData(data)
          return data
        })
        .catch((err) => {
          setError(err)
          if (config.throwOnError === true) {
            return Promise.reject(err)
          }
          return err
        })
    },
    [config.throwOnError, safeDispatch, setError, setData]
  )

  return {
    isIdle: state.stat === 'idle',
    isSuccess: state.stat === 'success',
    isError: state.stat === 'error',
    isLoading: state.stat === 'loading',
    run,
    setData,
    setError,
    ...state,
    retry
  }
}
