import { useCallback } from 'react'

import { useAuth } from './useAuth'
import { request } from '@/service'

export const useReuqest = () => {
  const { user } = useAuth()

  return useCallback(
    (...[endpoint, config]: Parameters<typeof request>) => {
      return request(endpoint, { ...config, token: user?.token })
    },
    [user]
  )
}

